.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  flex: 1;
  background-color:aliceblue;
  overflow: visible;
}

.App-header {
  position: relative;
  display:flex;
  height: 100vh;

}
.App-header:hover {
}
.App-background{
  background-image: url("./assets/background.jpg");
  filter: blur(8px);
  -webkit-filter: blur(8px);
  flex:1;
  background-position: center;
  background-size: cover;
}
.box{
  display:flex;
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0, 0.4); /* Black w/opacity/see-through */
  color: white;
  font-weight: bold;
  border: 3px solid #f1f1f1;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  border-radius: 10px;
  width: 80%;
  padding: 20px;
  text-align: center;
}
.box-info{
  display: flex;
  flex:1;
  flex-direction: row;
  justify-content: space-around;
}

.box-img{
  display: flex;
  height: 25vmax;
  width: 25vmax;
  align-items: center;
  justify-content: center;
  
}
.App-logo {
  position: absolute;
  height: 20vmax;
  width: 20vmax;
  box-shadow: black 2px 2px 5px;
  border-radius: 100%;
}



.App-header .box-info div div:nth-child(1){
  position: absolute;
  align-items: center;
  justify-content: center;
  background-color:#ff80b2; 
  border-radius: 100%;
  animation: App-logo-down infinite 4s linear;  
}
.App-header .box-info div div:nth-child(2){
  position: absolute;
  align-items: center;
  justify-content: center;
  background-color:#ff99c1; 
  border-radius: 100%;
  animation: App-logo-down infinite 1s linear;  
}
.App-header .box-info div:hover div:nth-child(1){
  transform: scale(1.2);
}
.App-header .box-info div:hover div:nth-child(2){
  transform: scale(1.2);
}
.App-header .box-info div:hover .App-logo {
  box-shadow: black 2px 2px 15px;
  border-radius: 100%;
  transform: scale(1.2);
  animation:  App-logo-scaleIn normal 0.6s linear;
}
.bio{
  align-self: center;
  justify-content: center;
  flex:1;
  font-size: 1.8vmax;
}
.icon{
height: 3vmax;
width: 3vmax;
}
.box-icon{
  position: absolute;
 display: flex;
 align-self: flex-end;
 align-items: center;
 justify-content: center;
 border-radius: 10px;
 width: max-content;
 background-color: #f1f1f1;
}

@media screen and (max-width: 400px) {
  .box{
    flex-direction: column;
  }
}
@keyframes App-logo-down {
  from {
    height: 25vmax;
    width: 25vmax;
  }
  to {
    height: 20vmax;
    width: 20vmax;  
  }
}